import React, { useState } from 'react';
import SubmitButton from '../../../view-utils/SubmitButton';
import { Container, Center } from '../../../view-utils/Framework';
import styled from 'styled-components';
import { withRouter } from 'react-router-dom';
import { Alert } from '@material-ui/lab'
import { ItrendLogo } from
  '../../../../containers/DefaultLayout/LogoThemed/ItrendLogo';
import { LandingLayout } from '../../Landing/components/LandingLayout';
import {Helmet} from "react-helmet";
import { FacebookIcon, FacebookShareButton, TelegramIcon,TwitterIcon, WhatsappIcon, TelegramShareButton, TwitterShareButton, WhatsappShareButton, } from "react-share";
import friends from "../../../../assets/icons/friends.svg";
const HeaderStyled = styled.header`
  text-align: center;
  font-size: 1.7em;
`
const landingContainer = {
  marginTop: '100px',
  textAlign: 'center'

};
const Wrapper = styled.div`
  text-align: center;

`
const InputStyled = styled.input`

  padding: 6px;
  text-align: center;
  border: none;
  font-size: 1.2em;
  border-bottom: dashed 1px white;
  width: 50%;
  height:49px
 `
const GeneralSection = props => {
  const { history } = props;


  const [state, setState] = useState({
    error: false,
    message: '',
    inputData: {
      name: '',
    }
  });
  const inputChange = e => {
    let inputData = state.inputData;
    const name = e.target.name;
    inputData[name] = e.target.value;
    setState({ inputData });
  }

  const OnSubmit = (event) => {
    let name = state.inputData.name;
    const urlParams = new URLSearchParams(props.location.search).get("id");
    const link = urlParams;
    event.preventDefault();
    const localStore = {
      username: name,
      link: link,
      quizTaken: true
    }
    const responseFromStore = JSON.parse(localStorage.getItem('response'));

    if (!responseFromStore) {
      localStorage.setItem('response', JSON.stringify(localStore));
      history.push(`/answer/${name}/${link}`);
    }
    else if (responseFromStore.username !== name) {
      localStorage.removeItem('response');
      localStorage.setItem('response', JSON.stringify(localStore));
      history.push(`/answer/${name}/${link}`);
    }
    else {
      history.push(`/results/${name}/${link}`);

    }
  }
  //const OnSubmitFunc = OnSubmit.bind(this);


  const { inputData, error, message } = state;
  const userId = props.match.params.userId;
  return (
    <div>

      <LandingLayout></LandingLayout>
      <Helmet>
      <title>response for ${userId}</title>
            <meta name="description" content={`How well do you know ${userId}.. Come on, check it out. I wanna know too.. So tap this to see`} />
            <meta property="og:title" content="click to respond" />
            <meta property="og:image" content={friends} />
      </Helmet>




      <Container style={landingContainer} className="center">
        <ItrendLogo className="mb-1" checkBackground />
        <Wrapper>
          <HeaderStyled>
            Hello, enter your name to continue
    </HeaderStyled>
          <Container>
            <Center>
              <div className='start-page'>
                <Alert hidden={!error} severity="error">{message}</Alert>
                <InputStyled

                  type='text'
                  placeholder='Enter Name'
                  name="name"
                  ref={(c) => state.name = c}
                  value={inputData.name}
                  onChange={inputChange}
                  error={error}
                  id='name' />
                <SubmitButton
                  className='start-button'
                  id='startQuizButton'
                  type="button"
                  onClick={OnSubmit}
                >
                  Start!
        </SubmitButton>
              </div>
              <WhatsappShareButton url={'https://itrend.fun'}>
                  <WhatsappIcon size={32} round={true} />
                </WhatsappShareButton>
                <TelegramShareButton url={'https://itrend.fun'}>
                  <TelegramIcon size={32} round={true} />
                </TelegramShareButton>
                <FacebookShareButton url={'https://itrend.fun'}>
                  <FacebookIcon size={32} round={true} />
                </FacebookShareButton>

                <TwitterShareButton url={'https://itrend.fun'}>
                  <TwitterIcon size={32} round={true} />
                </TwitterShareButton>

            </Center>
          </Container>

        </Wrapper>
      </Container>
    </div>
  )

}

export default withRouter(GeneralSection);
